@import url('https://fonts.googleapis.com/css2?family=Assistant:wght@100;200;300;400;500;600;700;800&display=swap');

*{
  font-family: Assistant;
  padding:0;
  margin:0;
}


@keyframes mymove {
  from {background-color: white;}
  to {background-color: lightyellow;}
}
body {
  padding-top: 15px;
  background: white;
  animation: mymove 6s infinite alternate;
  margin: auto;
  padding:80px;
  margin-right: 50px;
  margin-left: 50px;


  
}
.header{
  display:flex;
  justify-content: space-between;
  align-items: center;
}
.a{
  display:flex;
  gap:70px;
  align-items: center;
}
.link{
  padding-right: 30px;
  color:black;
  outline:none;
  font-size: 1.1rem;
  font-weight: 200;
  text-decoration: none;
  position: relative;
  
}
.link:hover::before {
  bottom: 0;
  height: 100%;
  color:white;
}
.link::before {
  content: '';
  background-color: hsla(196, 61%, 58%, .75);
  position: absolute;
  left: -3px;
  bottom: 3px;
  width: 70%;
  height: 4px;
  z-index: -1;
  transition: all .3s ease-in-out;

}
.cart{
  color:black;
  text-decoration: none;
  font-weight:100;
}
.logo{
  font-weight: 800;
  font-size:1.5rem;
  padding-right: 15px;
  text-transform: uppercase;
  background-image: linear-gradient(
    -225deg,
    #231557 0%,
    #44107a 29%,
    #ff1361 67%,
    #fff800 100%
    );
    background-size: auto auto;
    background-clip: border-box;
    background-size: 200% auto;
    color: #fff;
    background-clip: text;
    text-fill-color: transparent;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    animation: textclip 2s linear infinite;
    display: inline-block;
}
@keyframes textclip {
  to {
    background-position: 200% center;
  }
}
.home{
  display:flex;
  flex-flow: column;
  padding: 55px;

}
.footer{
  font-weight: 100;
  text-align: center;
  color:black;
  display:flex;
  justify-content: center;
  margin-top: 20px;
  padding:25px;
}
.h1{
  font-size:3rem;
  font-weight: 900;
  text-align: center;
}
.h2{
  font-weight: 100;
  text-align: center;
}
.imgs{
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 50%;
}
.imgProduct{
  display: block;
  margin-left: auto;
  margin-right: auto;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  max-width: 100%;
  max-height: 100%;
  margin: auto;
  display: block;
  object-fit: contain;
}
.product{
  padding:5px;
  margin:15px;
}

.boxProduct{
  display:flex;
  justify-content: center;
  flex-flow: column;
  text-align: center;
  align-items: center;
  padding:5px;  
}
.productName{
  font-size: 1.2rem;
  font-weight: 700;
  color: black;
  text-decoration: none;
}
.btn{
  background-color: black;
  color:white;
  padding:10px;
  margin-top:15px;
  font-weight: 100;
  display:block;
  width:30%;
  border:none;
  font-size:1.4rem;
  transition: 0.5s ease-out;

}
.btn:hover{
  transform: translateY(-0.25em);
}

.btns{
  background-color: white;
  border:1px solid black;
  color:black;
  padding:10px;
  margin-top:15px;
  font-weight: 100;
  font-size:1.4rem;
  transition: 0.5s ease-out;

}
.btns:hover{
  transform: translateY(0.25em);
}

a:link { text-decoration: none; }


a:visited { text-decoration: none; }


a:hover { text-decoration: none; }


a:active { text-decoration: none; }
.productPrice{
  font-weight: 200;
  font-size: 1.2rem;
  color: black;
  text-decoration: none;
  text-decoration: none;
}
.productNames{
  font-size: 1.8rem;
  font-weight: 800;
  color: black;
  text-decoration: none;
}
.productPrices{
  font-weight: 200;
  font-size: 1.4rem;
  color: black;
  text-decoration: none;
  text-decoration: none;
}
.productsBox{
  margin-top:25px;
  display: flex;
  flex-wrap: wrap;
}
.imgProduct{
  width:250px;
  height:auto;
}
.imgProducts{
  margin-top: 45px;
  width:300px;
  height:auto;
  border-radius:5px;
}
.imgProductz{
  width:130px;
  height:auto;
  border-radius:5px;
}
.cartProducts{
  margin-top:20px;
  display:flex;
  flex-flow:wrap;
  justify-content: center;
  align-items: flex-end;
  padding:15px;
  gap:15px;
}
.cartItem{
  display: flex;
  flex-flow: column;
  text-align: center;
  justify-content: center;
  
}
.cartItems{
  display: flex;
  flex-flow: column;
  justify-content: center;
  text-align: center;
}
.amount{
  font-size: 1.4rem;
}
.change{
  display:flex;
  justify-content: space-around;
  align-items: center;
  gap:10px;
}
.button{
  background-color: black;
  color:white;
  font-weight: 100;
  border:none;
  padding-left: 10px;
  padding-right: 10px;
  font-size:1.4rem;
  transition: 0.5s ease-out;
}
.totali{
  display:flex;
  flex-flow: column;
  justify-content: center;
  align-items: center;
  margin-top:15px;
}
.totalo{
  font-size: 1.5rem;
  font-weight: 600;
}
.totalPrice{
  font-size: 1.3rem;
  font-weight: 200;
}
@media only screen and (max-width: 900px) {
  body {
    padding-top: 35px;
    margin-top:25px;
    background: white;
    animation: mymove 6s infinite alternate;
    margin: auto;
    padding:10px;
    margin-right: 5px;
    margin-left: 5px;
  }  
  .imgs{
    width:340px;
    height:auto;
  }
  header{
    display:flex;
    justify-content: space-between;
    align-items: center;
  }
  .a{
    margin-top: 30px;;

    display:flex;
    gap:10px;
    align-items: center;
  }
  .link{
    padding-right: 10px;
    color:black;
    outline:none;
    font-size: 1.1rem;
    font-weight: 200;
    text-decoration: none;
    position: relative;
    
  }
  .link:hover::before {
    bottom: 0;
    height: 100%;
    color:white;
  }
  .link::before {
    content: '';
    background-color: hsla(196, 61%, 58%, .75);
    position: absolute;
    left: 0;
    bottom: 3px;
    width: 90%;
    height: 4px;
    z-index: -1;
    transition: all .3s ease-in-out;
  
  }
  .b{
    margin-top: 30px;;
  }
  .cart{
    color:black;
    text-decoration: none;
    font-weight:100;
  }
  .home{
    display:flex;
    flex-flow: column;
    padding: 10px;
    height:100vh;
    overflow-y:unset;
  }
  .h1{
    margin-top:30px;
    font-size:2.5rem;
    font-weight: 900;
    text-align: center;
  }
  .h2{
    font-size:1.2rem;
    font-weight: 100;
    text-align: center;
  }
  .imgProduct{
    height:auto;
    width:130px;
    display: block;
    margin-left: auto;
    margin-right: auto;
  }
  .imgProducts{
    margin-top: 20vh;
    width:250px;
    height:auto;
  }
  .btn{
    width:60%;
  }
  .btns{
    width:100%;
  }
}